import React from "react";
import "./Scan.css";
import * as ScanditSDK from "scandit-sdk";
import { Redirect } from "react-router-dom";

class Scan extends React.Component {
	state = {
		barcode: undefined,
		bcProductId: undefined,
	};

	componentDidMount = () => {
		// inside an async function
		ScanditSDK.configure(
			"",
			{
				engineLocation:
					"https://cdn.jsdelivr.net/npm/scandit-sdk@5.x/build/",
			}
		)
			.then(() => {
				return ScanditSDK.BarcodePicker.create(
					document.getElementById("scandit-barcode-picker"),
					{
						// enable some common symbologies
						scanSettings: new ScanditSDK.ScanSettings({
							enabledSymbologies: [
								"ean8",
								"ean13",
								"upca",
								"upce",
								"QR",
							],
						}),
					}
				);
			})
			.then((barcodePicker) => {
				// barcodePicker is ready here, show a message every time a barcode is scanned
				barcodePicker.on("scan", (scanResult) => {
					this.handleBarcode(scanResult.barcodes[0].data);
				});
			});
	};

	// check compatibility

	handleBarcode = (res) => {
		if (res) {
			console.log("barcode", res);
			//détecté par barcode scanner
			let arr = res.split("/");
			console.log(arr);

			this.setState({ barcode: arr[0] });

			if (arr.length === 2) {
				this.setState({ bcProductId: arr[1] });
			}
		}
	};

	/*
  setModeScan = () => {
    this.setState({ QRcodeScan: !this.state.QRcodeScan });
  };
  */

	render = () => {
		if (this.state.barcode) {
			return <Redirect to={"/products/" + this.state.barcode} />;
		}
		return (
			<React.Fragment>
				<div id="background-scan">
					<span
						class="close"
						onClick={() => this.props.onCrossClicked(false)}
					>
						&times;
					</span>
					<div id="scandit-barcode-picker"></div>
				</div>
			</React.Fragment>
		);
	};
}

export default Scan;
